@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --background: #FFFFFF;
  --foreground: #1D1D1F;
  --text-primary: #1D1D1F;
  --text-secondary: #6E6E73;
  --border: #E0E0E0;
  --blue: #0070F3;
}

.dark {
  --background: #0A0A0A;
  --foreground: #FAFAFA;
  --text-primary: #FAFAFA;
  --text-secondary: #A3A3A3;
  --border: #1F1F1F;
  --blue: #0070F3;
}

body {
  background: var(--background);
  color: var(--foreground);
}

.text-display {
  @apply text-5xl font-bold tracking-tight;
  color: var(--foreground);
}

.page-title {
  @apply text-4xl font-bold tracking-tight;
  color: var(--foreground);
}

.text-brand {
  @apply text-2xl font-bold italic tracking-tight;
  color: var(--foreground);
}

.section-title {
  @apply text-2xl font-bold tracking-tight;
  color: var(--foreground);
}

.text-headline {
  @apply text-xl font-semibold;
  color: var(--foreground);
}

.text-button-lg {
  @apply text-base font-semibold;
  color: var(--foreground);
}

.text-primary {
  @apply text-base;
  color: var(--foreground);
}

.text-nav {
  @apply text-base;
  color: var(--text-secondary);
}

.text-caption {
  @apply text-sm;
  color: var(--text-secondary);
}

.text-tag {
  @apply text-xs;
}